<template>
  <div id="app" :class="{ 'hide-menu': !isMenuVisible || !user }">
    <Header
      title="Cod3r - Base de conhecimento"
      :hideToggle="!user"
      :hideUserDropdown="!user"
    />
    <Loading v-if="validatingToken" />
    <Content v-else />
    <Menu v-if="user" />
    <!-- <button @click="validateAdmin">validate</button> -->
    <Footer />
  </div>
</template>

<script>
import { mapState } from "vuex";
import Content from "@/components/template/Content";
import Footer from "@/components/template/Footer";
import Header from "@/components/template/Header";
import Menu from "@/components/template/Menu";
import Loading from "@/components/template/Loading";
import axios from "axios";
import { baseApiUrl, userKey } from "@/global";

export default {
  name: "App",
  components: {
    Content,
    Footer,
    Header,
    Menu,
    Loading,
  },
  computed: mapState(["isMenuVisible", "user"]),
  data: function () {
    return {
      validatingToken: true,
    };
  },
  methods: {
    async validateToken() {
      this.validatingToken = true;

      const json = localStorage.getItem(userKey);
      const userData = JSON.parse(json);
      this.$store.commit("setUser", null);

      if (!userData) {
        this.validatingToken = false;
        this.$router.push({ name: "auth" });
        return;
      }

      const resp = await axios.post(`${baseApiUrl}/validateToken`, userData);

      if (resp.data) {
        this.$store.commit("setUser", userData);
        if (this.$mq === "xs" || this.$mq === "sm") {
          this.$store.commit("toggleMenu", false);
        }
      } else {
        localStorage.removeItem(userKey);
        this.$router.push({ name: "auth" });
      }

      this.validatingToken = false;
    },
    // async validateAdmin() {
    //   const json = localStorage.getItem(userKey);
    //   const userData = JSON.parse(json);
    //   const resp = await axios.post(`${baseApiUrl}/validateIsAdmin`, userData);
    //   console.log("validateAdmin: " + resp.data)
    // },
  },
  mounted() {
    this.validateToken();
  },
};
</script>

<style>
* {
  font-family: "Lato", sans-serif;
  margin: 0;
}

#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  height: 100vh;
  display: grid;
  grid-template-columns: 300px 1fr;
  grid-template-rows: 60px 1fr 40px;
  grid-template-areas:
    "header header"
    "menu content"
    "menu footer";
}

#app.hide-menu {
  grid-template-areas:
    "header header"
    "content content"
    "footer footer";
}
</style>