<template>
  <div class="article-admin">
    <b-form>
      <input id="article-id" type="hidden" v-model="article.id" />

      <b-row>
        <b-col xl="12">
          <b-form-group label="Nome:" label-for="article-name">
            <b-form-input
              id="article-name"
              type="text"
              v-model="article.name"
              required
              :readonly="mode === 'remove'"
              placeholder="Informe o nome do artigo..."
            >
            </b-form-input>
          </b-form-group>
        </b-col>
      </b-row>

      <b-row>
        <b-col xl="12">
          <b-form-group label="Descrição:" label-for="article-description">
            <b-form-input
              id="article-description"
              type="text"
              v-model="article.description"
              required
              :readonly="mode === 'remove'"
              placeholder="Informe a Descrição do artigo..."
            >
            </b-form-input>
          </b-form-group>
        </b-col>
      </b-row>

      <b-row>
        <b-col xl="12">
          <b-form-group
            v-show="mode !== 'remove'"
            label="Imagem (URL):"
            label-for="article-imageUrl"
          >
            <b-form-input
              id="article-imageUrl"
              type="text"
              v-model="article.imageUrl"
              required
              placeholder="Informe a Imagem (URL) do artigo..."
            >
            </b-form-input>
          </b-form-group>
        </b-col>
      </b-row>

      <b-row>
        <b-col xl="12">
          <b-form-group
            v-show="mode !== 'remove'"
            label="Categoria:"
            label-for="article-categoryId"
          >
            <b-form-select
              id="article-categoryId"
              :options="categories"
              v-model="article.categoryId"
            />
          </b-form-group>
        </b-col>
      </b-row>

      <b-row>
        <b-col xl="12">
          <b-form-group
            v-show="mode !== 'remove'"
            label="Autor:"
            label-for="article-userId"
          >
            <b-form-select
              id="article-userId"
              :options="users"
              v-model="article.userId"
            />
          </b-form-group>
        </b-col>
      </b-row>

      <b-row>
        <b-col xl="12">
          <b-form-group
            v-show="mode !== 'remove'"
            label="Conteudo:"
            label-for="article-content"
          >
            <VueEditor
              v-model="article.content"
              placeholder="Informe o conteudo de artigo..."
            />
          </b-form-group>
        </b-col>
      </b-row>

      <b-row class="mb-3">
        <b-col xs="12">
          <b-button variant="primary" v-if="mode === 'save'" @click="save"
            >Salvar</b-button
          >
          <b-button variant="danger" v-if="mode === 'remove'" @click="remove"
            >Excluir</b-button
          >
          <b-button class="ml-2" @click="reset">Cancelar</b-button>
        </b-col>
      </b-row>
    </b-form>
    <b-table hover striped :items="articles" :fields="fields">
      <template slot="actions" slot-scope="data">
        <b-button
          variant="warning"
          @click="loadArticle(data.item)"
          class="mr-2"
        >
          <i class="fa fa-pencil"></i>
        </b-button>
        <b-button variant="danger" @click="loadArticle(data.item, 'remove')">
          <i class="fa fa-trash"></i>
        </b-button>
      </template>
    </b-table>
    <b-pagination
      size="md"
      v-model="page"
      :total-rows="count"
      :per-page="limit"
    />
  </div>
</template>

<script>
import { VueEditor } from "vue2-editor";
import { baseApiUrl, showError } from "@/global";
import axios from "axios";
export default {
  name: "ArticleAdmin",
  components: { VueEditor },
  data: function () {
    return {
      mode: "save",
      article: {},
      articles: [],
      categories: [],
      users: [],
      page: 1,
      limit: 0,
      count: 0,
      fields: [
        { key: "id", label: "Código", sortable: true },
        { key: "name", label: "Nome", sortable: true },
        { key: "description", label: "Descrição", sortable: true },
        { key: "actions", label: "Ações" },
      ],
    };
  },
  methods: {
    loadArticles() {
      const url = `${baseApiUrl}/articles?page=${this.page}`;
      axios.get(url).then((resp) => {
        // this.articles = resp.data
        this.articles = resp.data.data;
        this.count = resp.data.count;
        this.limit = resp.data.limit;
      });
    },
    reset() {
      this.mode = "save";
      this.article = {};
      this.loadArticles();
    },
    save() {
      const method = this.article.id ? "put" : "post";
      const id = this.article.id ? `${this.article.id}` : "";
      const url = `${baseApiUrl}/articles/${id}`;
      axios[method](url, this.article)
        .then(() => {
          this.$toasted.global.defaultSuccess();
          this.reset();
        })
        .catch(showError);
    },
    remove() {
      const id = this.article.id;
      const url = `${baseApiUrl}/articles/${id}`;
      axios
        .delete(url)
        .then(() => {
          this.$toasted.global.defaultSuccess();
          this.reset();
        })
        .catch(showError);
    },
    loadArticle(article, mode = "save") {
      this.mode = mode;
      // this.article = { ...article };
      axios
        .get(`${baseApiUrl}/articles/${article.id}`)
        .then((resp) => (this.article = resp.data));
    },
    loadCategories() {
      const url = `${baseApiUrl}/categories`;
      axios.get(url).then((resp) => {
        this.categories = resp.data.map((category) => {
          return { value: category.id, text: category.path };
        });
      });
    },
    loadUsers() {
      const url = `${baseApiUrl}/users`;
      axios.get(url).then((resp) => {
        this.users = resp.data.map((user) => {
          return { value: user.id, text: `${user.name} - ${user.email}` };
        });
      });
    },
  },
  watch: {
    page() {
      this.loadArticles()
    }
  },
  mounted() {
    this.loadArticles();
    this.loadCategories();
    this.loadUsers();
  },
};
</script>

